import React, { useEffect, useState } from "react";
import PageTitle from "../../components/PageTitle/PageTitle";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";

import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import axios from "axios";
import {
  Button,
  Grid,
  Input,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "1px #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

export default function GarmentStyle() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [garmentList, setGarmentList] = useState([]);
  const [selectedGarments, setSelectedGarments] = useState([]);
  const [styleType, setStyleType] = useState(0);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleChange = (event) => {
    setSelectedGarments(event.target.value);
  };

  const handleSubmit = () => {
    console.log(selectedGarments);
  };
  const fetchGarmentLIst = () => {
    axios
      .get("http://localhost:3232/api/allGarments")
      .then((response) => {
        const { status, data } = response.data;
        if (status) {
          console.log(data);
          setGarmentList(data);
        }
      })
      .catch();
  };
  useEffect(() => {
    fetchGarmentLIst();
  }, []);
  return (
    <>
      <PageTitle
        title="Style List"
        button={
          <Button
            variant="contained"
            size="medium"
            color="secondary"
            onClick={handleOpen}
          >
            Add Garment
          </Button>
        }
      />

      <Grid>
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={open}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 1000,
          }}
        >
          <Fade in={open}>
            <div className={classes.paper}>
              <h2 id="transition-modal-title">Add new Garment</h2>
              <div>
                <TextField
                  id="standard-basic"
                  label="Title"
                  name="title"
                  // value={newOption.title}
                  onChange={handleChange}
                />
              </div>
              <div>
                <InputLabel id="demo-mutiple-name-label">Garments</InputLabel>
                <Select
                  labelId="demo-mutiple-name-label"
                  id="demo-mutiple-name"
                  multiple
                  value={selectedGarments}
                  onChange={handleChange}
                  input={<Input />}
                  MenuProps={MenuProps}
                >
                  {garmentList.map((item) => (
                    <MenuItem key={item._id} value={item._id}>
                      {`${item.title} | ${item.gender}`}
                    </MenuItem>
                  ))}
                </Select>
              </div>
              <div>
                <TextField
                  // id="outlined-select-currency"
                  select
                  label="Style Type"
                  name="styleType"
                  value={styleType}
                  helperText="Select Style Type."
                  variant="outlined"
                  // value={styleOption}
                  onChange={(e) => setStyleType(e.target.value)}
                >
                  {[
                    { label: "Ready Made", value: 0 },
                    { label: "custom", value: 1 },
                  ].map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </TextField>
              </div>

              <div>
                <Button variant="outlined">Cancel</Button>
                <Button
                  variant="outlined"
                  color="primary"
                  onClick={handleSubmit}
                >
                  Submit
                </Button>
              </div>
            </div>
          </Fade>
        </Modal>
      </Grid>
    </>
  );
}
