import React, { useState, useEffect } from "react";
import { Button, Grid } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import PageTitle from "../../components/PageTitle/PageTitle";
import IconButton from "@material-ui/core/IconButton";
import PhotoCamera from "@material-ui/icons/PhotoCamera";
import axios from "axios";
import { mdiCookieSettingsOutline } from "@mdi/js";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";

const useStyles = makeStyles((theme) => ({
  form1: {
    "& > *": {
      margin: theme.spacing(1),
      width: "100%",
    },
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  input: {
    display: "none",
  },
}));

export default function AddGarment() {
  const classes = useStyles();
  const [tableData, setTableData] = useState([]);
  const [newOption, setNewOption] = useState({
    title: "",
    gender: "",
    image: "",
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("image", newOption.image);
    formData.append("gender", newOption.gender);
    formData.append("title", newOption.title);

    console.log(newOption);
    // axios
    //   .post("http://localhost:3232/api/garmentType", formData)
    //   .then((res) => {
    //     console.log(res);
    //   })
    //   .catch((err) => {
    //     console.log(err);
    //   });
  };

  const columns = [
    {
      name: "_id",
      label: "ID",
      options: {
        filter: false,
        sort: false,
      },
    },
    {
      name: "title",
      label: "Title",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "gender",
      label: "Gender",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "image",
      label: "Image",
      options: {
        filter: false,
        sort: false,
      },
    },
    {
      name: "status",
      label: "Status",
      options: {
        filter: true,
        sort: true,
      },
    },
  ];

  const genders = [
    {
      value: "men",
      label: "Men",
    },
    {
      value: "women",
      label: "Women",
    },
  ];

  const [gender, setGender] = React.useState("men");

  const handleChangec = (event) => {
    setGender(event.target.value);
  };

  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleChange = (e) => {
    console.log(e.target.value)
    setNewOption({ ...newOption, [e.target.name]: e.target.value });
  };

  const handleImage = (e) => {
    setNewOption({ ...newOption, image: e.target.files[0] });
  };

  const fetchGarmentLIst = () => {
    axios
      .get("http://localhost:3232/api/allGarments")
      .then((response) => {
        const { status, data } = response.data;
        if (status) {
          console.log(data);
          setTableData(data);
        }
      })
      .catch();
  };

  useEffect(() => {
    fetchGarmentLIst();
  }, []);

  return (
    <>
      <PageTitle
        title="Grament List"
        button={
          <Button
            variant="contained"
            size="medium"
            color="secondary"
            onClick={handleOpen}
          >
            Add Garment
          </Button>
        }
      />

      <Grid container spacing={4}>
        <Grid>
          <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            className={classes.modal}
            open={open}
            onClose={handleClose}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 1000,
            }}
          >
            <Fade in={open}>
              <div className={classes.paper}>
                <h2 id="transition-modal-title">Add new Garment</h2>
                <div>
                  <TextField
                    id="standard-basic"
                    label="Garment Title"
                    name="title"
                    value={newOption.title}
                    onChange={handleChange}
                  />
                </div>
                <div>
                  <TextField
                    // id="outlined-select-currency"
                    select
                    label="Select Gender"
                    name="gender"
                    value={gender}
                    onChange={handleChangec}
                    helperText="Please select gender."
                    variant="outlined"
                    value={newOption.gender}
                    onChange={handleChange}
                  >
                    {genders.map((option) => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </TextField>
                </div>
                <div>
                  <input
                    accept="image/*"
                    className={classes.input}
                    style={{ display: "none" }}
                    id="raised-button-file"
                    type="file"
                    name="image"
                    onChange={handleImage}
                  />
                  <label htmlFor="raised-button-file">
                    <Button
                      variant="raised"
                      component="span"
                      className={classes.button}
                    >
                      Upload
                    </Button>
                  </label>
                </div>

                <div>
                  <Button variant="outlined">Cancel</Button>
                  <Button variant="outlined" color="primary" onClick={handleSubmit}>
                    Submit
                  </Button>
                </div>
              </div>
            </Fade>
          </Modal>
        </Grid>
      </Grid>
    </>
  );
}
